const padZero = (day) => {
  if (day < 10) {
    return `0${day}`;
  } else {
    return `${day}`;
  }
};

const formatDate = (year, month, day) => {
  let validDatePart = [];
  year && validDatePart.push(year);
  month && validDatePart.push(padZero(month));
  day && validDatePart.push(padZero(day));
  return validDatePart.join('-');
}

const formatDateObject = (dateObj) => {
  let validDatePart = [];
  validDatePart.push(dateObj.getFullYear());
  validDatePart.push(padZero(dateObj.getMonth()));
  validDatePart.push(padZero(dateObj.getDate()));
  return validDatePart.join('-');
}

const formatDateTime = (date = new Date()) => {
  let year = date.getFullYear();
  let month = date.getMonth();
  let day = date.getDate();
  let hour = date.getHours();
  let minute = date.getMinutes();
  let second = date.getSeconds();

  return `${year}-${padZero(month + 1)}-${padZero(day)} ${padZero(hour)}:${padZero(minute)}:${padZero(second)}`;
};

const formatMonth = (date = new Date()) => {
  let year = date.getFullYear();
  let month = date.getMonth();

  return `${year}-${padZero(month + 1)}`;
};

const uuid = () => {
  let s = [];
  let hexDigits = "0123456789ABCDEF";
  for (let i = 0; i < 36; i++) {
      s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
  }
  s[14] = "4";  // bits 12-15 of the time_hi_and_version field to 0010
  s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1);  // bits 6-7 of the clock_seq_hi_and_reserved to 01
  s[8] = s[13] = s[18] = s[23] = "-";

  let uuid = s.join("");
  return uuid;
}

export {
  padZero,
  formatDateTime,
  formatMonth,
  uuid,
  formatDate,
  formatDateObject
};